/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

nav ul{
  display: flex;
  justify-content: space-around;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Own styling starts here */

* {
	box-sizing: border-box;
}

html{
	height: 100%;
	background-color: #FBEEC1;
	margin: 0 10px;
}
/* Nav styling */

nav {
	height: 5vh;
	background-image: linear-gradient(#8D8741, #B3AD6B);
	margin: 0 -10px;
	color: white;
	font-size: 28px;
	font-weight: 700;
}

nav ul {
	height: 100%;
	display: flex;
	align-items: center;
}

/* Home Styling */

header {
	height: 11vh;
	text-align: center;
	font-weight: 900;
	padding: 10px;
	color: #333;
	font-family: 'Overpass', sans-serif;
}

header h1 {
	font-size: 50px;
	padding-bottom: 8px;
}

header h2 {
	font-size: 28px;
}

.home-wrapper {
	margin: 0 -10px;
}

.home-img-wrapper {
	position: relative;
	padding-top: 84vh;
}

.home-durian {
	background-image: url(./assets/home-durian.jpg);
}

.home-img {
	background-size: cover;
	border-radius: 4px;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	background-position: center;
}

/* Shop styling */

.shop {
	display: grid;
	grid-template-columns: 1fr 250px;
	padding: 26px;

}

.info {
	color: #222;
	font-size: 20px;
}

.info h2 {
	font-size: 28px;
	font-weight: 700;
	padding-bottom: 20px;
	text-align: center;
}

.info ul li {
	padding-bottom: 15px;
}

.items-display {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
	grid-gap: 15px;
	justify-content: start;
	padding-right: 20px;
}

.item-card {
	text-align: center;
	font-weight: 600;
	background-color: #8D8741;
	border-radius: 4px;
	color: #efefef;
	border: 1px solid #333;
	font-size: 20px;
}

.image-wrapper {
	padding-top: 100%;
	position: relative;
}

.image {
	background-size: cover;
	border-radius: 4px;
	position: absolute;
	width: 95%;
	height: 95%;
	top: 2.5%;
	left: 2.5%;
}

.item-info {
	padding: 10px;
	font-weight: 700;
	font-size: 28px;
}

.item-qty {
	padding: 5px;
}

.reserve-btn {
	width: 100%;
	padding: 10px;
	font-size: 18px;
	font-weight: 700;
	border: none;
	outline: none;
	background-color: #DAAD86;
	border: 1px solid #999;
	cursor: pointer;
	color: #111;
}

/* Cart Styling */

.table-wrapper {
	display: flex;
	justify-content: center;
}

table {
	border-collapse: separate;
	border-spacing: 50px 10px;
	color: #333;
	font-weight: 700;
	font-size: 18px;
	margin: 6px -26px 0 -26px;
}

th {
	text-align: start;
	font-size: 28px;
}

.cart-img-wrapper {
	position: relative;
	height: 190px;
	width: 160px;
	background-color: #8D8741 ;
	border-radius: 4px;
}

.cart-img {
	background-size: cover;
	height: 85%;
	width: 95%;
	border-radius: 4px;
	position: absolute;
	top: 2.5%;
	left: 2.5%;
}

.cart-item {
	position: absolute;
	bottom: 6px;
	left: 5px;
	color: #efefef;
}

.align-end {
	text-align: end;
}

.order-btn {
	width: 100%;
	padding: 10px;
	font-weight: 700;
	font-size: 20px;
	color: #333;
}

tfoot {
	font-size: 28px;
}

@media screen and (max-width: 850px) {
	.cart-img-wrapper {
		height: auto;
		width: 140px;
	}

	.cart-img {
		display: none;
	}

	.cart-item {
		top: 10%;
		font-size: 16px;
	}

	.order-btn {
		font-size: 16px;
	}
}


